import { actions } from '../model/type_maps'

export default function() {
  if (!history.onstatechange) {
    let url = location.href
    history.onstatechange = e => {
      localStorage.setItem('prev', location.href)
      this.track(actions.LEAVE)
      this.setEnv('refer', location.href)
      this.updateSid()
      this.recoverReportParams()
    }
    window.addEventListener('popstate', e => {
      history.onstatechange({ state: e.state, url: url })
      url = location.href
    })
    window.addEventListener('beforeunload', e => {
      history.onstatechange({ state: e.state, url: url })
    })
    ;(function(history) {
      let pushState = history.pushState
      let replaceState = history.replaceState
      history.pushState = function(state) {
        if (typeof history.onstatechange == 'function') {
          history.onstatechange({ state: state, url: location.href })
        }
        return pushState.apply(history, arguments)
      }
      history.replaceState = function(state) {
        if (typeof history.onstatechange == 'function') {
          history.onstatechange({ state: state, url: location.href })
        }
        return replaceState.apply(history, arguments)
      }
    })(window.history)
  }
}
