import env from '../model/env'
import logData from '../model/log_data'
import { urlKeys, actionKeys } from '../model/type_maps'
/**
 * 日志类
 */
class Log {
  static plugins = []
  static use(plugin) {
    Log.plugins.push(plugin)
  }
  s = 0
  start = 0
  /**
   * 构造方法
   * @param {obj} config
   * new Log({
   *  pf: 7,
   *  bu:5
   * })
   */
  constructor(config) {
    this.loadPlugins()
    this.updateSid()
    this.xmlHttp = new XMLHttpRequest()
    this.reportUrl = process.env.REPORT_URL
    this.baseParams = env
    this.reportParams = Object.assign({}, logData, config)
  }
  /**
   * 初始化插件
   */
  loadPlugins() {
    Log.plugins.forEach(plugin => {
      plugin.call(this, Log)
    })
  }
  /**
   *
   * @param {string} key 环境参数key
   * @param {string} value 环境参数值
   */
  setEnv(key, value) {
    this.baseParams[key] = value
  }
  /**
   *
   * @param {string} key 上报参数key
   * @param {string} value 上报参数值
   */
  set(key, value) {
    this.reportParams[key] = value
  }
  updateSid() {
    this.s = `${Date.now()}${Math.floor(Math.random() * 10000)}`
    this.start = Date.now()
  }
  recoverReportParams() {
    delete this.reportParams['consumer_uid']
    delete this.reportParams['service_names']
  }
  /**
   * 上报方法
   * @param {string} action 上报动作
   * @param {string} event 上报事件内容
   */
  track(action, data) {
    try {
      let ret = this.analysisKeys(action)
      if (ret) {
        let k = ret.k
        let exParams = data || {}
        exParams.behavior = ret.behavior
        if (k === urlKeys['/site/'][1].k) {
          let during = Math.floor(Date.now() - this.start)
          exParams.event = during
        }
        let params = Object.assign(
          {},
          this.baseParams,
          this.reportParams,
          exParams
        )
        // let args = ''
        // for (let key in params) {
        //   args += `${args ? '&' : ''}${key}=${params[key]}`
        // }
        // let token = window.btoa(args)
        if(params.business_uid!='0'){
          let id = `${Date.now()}${Math.floor(Math.random() * 10000)}`
          let token = window.btoa(JSON.stringify(params))
          let src = `${this.reportUrl}?k=${k}&id=${id}&s=${this.s}&token=${token}`
          this.xmlHttp.open('GET', src)
          this.xmlHttp.send()
        }
      }
    } catch (error) {
      console.log(error)
    }
  }

  /**
   * 解析上报动作获取k值
   * @param {string} action 上报动作
   */
  analysisKeys(action) {
    let ret = 0
    switch (action.type) {
      case 'url':
        let path = window.location.pathname
        let _key = Object.keys(urlKeys).find(item => {
          return path.indexOf(item) != -1
        })
        if (_key) {
          ret = urlKeys[_key][action.value]
        }
        break
      case 'action':
        ret = actionKeys[action.value]
        break
      default:
        break
    }
    return ret
  }
}

export default Log
